.team-member {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border: 1px solid black;
  border-bottom: 0;
  background: rgba(250, 250, 250, 0.7);
  overflow: hidden;
}
.team-list__item:first-child .team-member {
  border-top: 1px solid black;
}
.team-list__item:last-child .team-member {
  border-bottom: 1px solid black;
}
  .team-member__img {
    height: 50px;
    width: 30%;
    background-size: cover;
    background-position: center center;
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }
  .team-member__name {
    width: 55%;
    margin: 0;
    font-size: 1.6rem;
    font-weight: 400;
    padding: 6px;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
  }
  .team-member__remove {
    padding: 18px 0;
    background: darkred;
    color: #f5f5f5;
    border-radius: 0;
    border: 0;
    width: 15%;
  }



